import React from 'react';
import classes from './Button.module.scss';

const Button = (props) => {
    return <button 
                className={[classes.Button, classes[props.btnType]].join(' ')}
                type='submit'
                onClick={props.clicked}
                disabled={props.disabled}>
                {props.children}
            </button>
}

export default React.memo(Button);
